
.v_form {
  padding-bottom: 5px;
}
input {
  font-size: large;
  font-family: 'Farsan';
}
input[type=number] {
  width: 10% !important;
}
input[type=text], input[type=number] {
  border: none;
  width: 50%;
  padding: 5px 5px;
  margin: 8px 0;
  box-sizing: border-box;
  border-bottom: 1px solid #ff8000;
}
input[type=text]:focus, input[type=number]:focus {
  outline: none;
  border-bottom: 2px solid #ff8000;
}
textarea {
  width: 50%;
  height: 99px;
  margin: 12px 20px;
  border: none;
  border-bottom: 2px solid #ff8000;
  resize: none;
  font-family: 'Farsan';
  font-size: large;
}
textarea:focus {
  outline: none;
}
.v_form_submit {
  background-color: #ff9000;
  border: none;
  color: black;
  padding: 16px 32px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  font-family: 'Farsan';
  font-size: large;
}
.v_form_submit:focus {
  outline: 1px solid #000000;
}
.v_form_submit:active {
  outline: 1px solid #0f0f0f;
}
#submitted {
  color: #3CB600;
}



